import {Component, OnInit, AfterViewInit, ViewEncapsulation,Input,OnChanges, OnDestroy} from '@angular/core';
import { CalculatorAnalytics } from '../../services/calculator-analytics.service';
import { Subscription } from 'rxjs/Subscription';
import { CookieService } from '../../../../../shared/services/index';


declare var jQuery:any;
declare var google:any;
declare var moment: any;

@Component({
  selector: 'og-traffic-details',
  template: require('./traffic_details.component.html'),
  encapsulation: ViewEncapsulation.None,
})

export class TrafficDetailsComponent implements  AfterViewInit,OnChanges, OnDestroy, OnInit {
  @Input() calc: any;
  calc_id: string;
  geoLoader: string = 'loading';
  deviceLoader: string = 'loading';
  socialLoader: string = 'loading';
  browserLoader: string = 'loading';
  limit_alert: boolean = false;
  postData: any = {
      start_date: new Date(moment().subtract(10, 'days').format('YYYY-MM-DD')).toISOString().substr(0,10),
      end_date: new Date(moment().add(1, 'day').format('YYYY-MM-DD')).toISOString().substr(0,10)
  };

  private subs: Subscription[] = [];

  constructor(private _calcAnalytics: CalculatorAnalytics, private _cookieService: CookieService) {

  }

  ngAfterViewInit() {
    //Code
  }

  ngOnInit() {
    this.checkTrafficLimit();
  }

  checkTrafficLimit() {
    let storage = JSON.parse(this._cookieService.readCookie('storage'));
    let company = storage.company;
    this.limit_alert = company.current_limit.traffic === -1 || company.current_limit.traffic > company.current_usage.traffic ? false : true;
    //this.limit_alert  = true;
    //console.log(this.limit_alert, 'limit alert');
  }

  ngOnChanges() {
    if(this.calc) {
      if (this.calc.liveApp)
        this.calc_id = this.calc._id;
      else
        this.calc_id = this.calc.parentApp;

      this.postData.calc_id = this.calc_id;
      //get stats
      this.getStats();
    }
  }

  getStats() {
    //geo location data
    this.subs.push(this.getGeoStats());
    //device data
    this.subs.push(this.getDeviceStats());
    //social data
    this.subs.push(this.getSocialStats());
    //browser data
    this.subs.push(this.getBrowserStats());
  }

  getGeoStats() {
    this.geoLoader = 'loading';
    this.postData.type = 'country';
    return this._calcAnalytics.getTrafficStats(this.postData)
      .subscribe(
        (response: any[]) => {
          if (!response.length || (response.length == 1  && response[0][0] == '(not set)')) {
            this.geoLoader = 'Available Soon !';
          } else {
            response = response.map(val => [val[0], Number(val[2])]);
            response.unshift(['Country', 'Page Views']);
            this.drawRegionsMap(response);
            this.geoLoader = 'done';
          }
        },
        (error: any) => {
          this.geoLoader = 'Something Went Wrong !';
        }
      );
  }

  getDeviceStats() {
    this.deviceLoader = 'loading';
    this.postData.type = 'deviceCategory';
    return this._calcAnalytics.getTrafficStats(this.postData)
      .subscribe(
        (response: any[]) => {
          if (response.length) {
            response = response.map(val => {
              if(val[0] == '(not set)')
                val[0] = 'Unknown device';
              return response = [val[0], Number(val[2])];
            });
            response.unshift(['Devices', 'Page Views']);
            this.drawPieChart(response, 'piechart');
            this.deviceLoader = 'done';
          } else {
            this.deviceLoader = 'Available Soon !';
          }
        },
        (error: any) => {
          this.deviceLoader = 'Something Went Wrong !';
        }
      );
  }

  getSocialStats() {
    this.socialLoader = 'loading';
    this.postData.type = 'socialNetwork';
    return this._calcAnalytics.getTrafficStats(this.postData)
      .subscribe(
        (response: any[]) => {
          if (response.length) {
            response = response.map(val => {
              if(val[0] == '(not set)')
                val[0] = 'Direct Access';
              return response = [val[0], Number(val[2])];
            });
            response.unshift(['Social', 'Page Views']);
            this.drawPieChart(response, 'piechart1');
            this.socialLoader = 'done';
          } else {
            this.socialLoader = 'Available Soon !';
          }
        },
        (error: any) => {
          this.socialLoader = 'Something Went Wrong !';
        }
      );
  }

  getBrowserStats() {
    this.browserLoader = 'loading';
    this.postData.type = 'browser';
    return this._calcAnalytics.getTrafficStats(this.postData)
      .subscribe(
        (response: any[]) => {
          if (response.length) {
            response = response.map(val => [val[0], Number(val[2])]);
            response.unshift(['Browser', 'Page Views']);
            this.drawPieChart(response, 'piechart2');
            this.browserLoader = 'done';
          } else {
            this.browserLoader = 'Available Soon !';
          }
        },
        (error: any) => {
          this.browserLoader = 'Something Went Wrong !';
        }
      );
  }

  drawRegionsMap(graphData: any[]) {
    var data = google.visualization.arrayToDataTable(graphData);
    var options = {'width':250,  'height':180,
      colors: ['#ce5050', '#f17a52', '#f9de71'],
      legend:'none'
    };
    var chart = new google.visualization.GeoChart(document.getElementById('regions_div'));
    chart.draw(data, options);
  }

  drawPieChart(graphData: any[], drawingArea: string) {
    var data = google.visualization.arrayToDataTable(graphData);
    var options = {
      'width':'680',  'height':'280',
      backgroundColor: 'transparent',
      legend: {textStyle: {color: '#8c9194', fontSize: '11px',  marginBottom: '5px'}},
      colors: ['#f38630', '#69d2e7', '#e0e4cc']
    };

    var chart = new google.visualization.PieChart(document.getElementById(drawingArea));
    chart.draw(data, options);
  }

  onDateSelect(date: any) {
    this.postData.start_date = new Date(date.start_date).toISOString().substr(0,10);
    this.postData.end_date = new Date(moment(new Date(date.end_date)).add(1, 'day').format('YYYY-MM-DD')).toISOString().substr(0,10);
    //refresh stats
    this.getStats();
  }

  ngOnDestroy() {
    this.unsubscribe();
  }

  unsubscribe() {
    this.subs.forEach(sub=>sub.unsubscribe());
  }
}



