import {Component, Input, OnInit, AfterViewInit, ViewEncapsulation, OnChanges, ChangeDetectorRef} from '@angular/core';
import {CalculatorAnalytics} from '../../services/calculator-analytics.service';

declare var jQuery: any;

@Component({
  selector: 'og-user-details-popup',
  template: require('./user_details_popup.component.html'),
  encapsulation: ViewEncapsulation.None,
})

export class UserDetailsPopupComponent implements OnInit, OnChanges, AfterViewInit {
  @Input() visitorKey: any;
  statsResult: any;

  constructor(private _calculatorAnalytics: CalculatorAnalytics, private cdr: ChangeDetectorRef) {

  }

  ngOnInit() {
    //Code
  }

  ngAfterViewInit(){
    //code
    jQuery(document).on('click','.clear-set', () => {
      //Hide the menus if visible
      jQuery('.user-detail-outer').fadeOut();
    });
  }

  ngOnChanges() {
    this.statsResult = undefined;
    if(this.visitorKey)
      this.getStatsInfo();
  }

  getStatsInfo() {
    /* get leads of user on page */
    this._calculatorAnalytics.getStats({ key: this.visitorKey, answered: true })
      .subscribe(
        (response: any) => {
          /** intialize data table */
          this.statsResult = response;
          var heightWindow = jQuery(window).height() - 153;
          setTimeout(function(){
            jQuery('.outer-slim').slimScroll({
            height: heightWindow
          })},500);
          this.cdr.detectChanges();
        },
        (error: any) => {
          console.log(error);
        }
      );
  }
}



