import {Component, OnInit, AfterViewInit, ViewEncapsulation, Output, EventEmitter} from '@angular/core';
declare var jQuery:any;
declare var moment: any;

@Component({
  selector: 'og-date-range-picker',
  template: require('./date_range_picker.component.html'),
  encapsulation: ViewEncapsulation.None
})

export class DateRangePickerComponent implements  AfterViewInit {
  @Output() date: EventEmitter <String> = new EventEmitter <String>();

  ngAfterViewInit() {
    var self:any = this;
    // TODO error in these two functions so commented it.
    //Data range Picker
    jQuery('.input-daterange-datepicker').daterangepicker({
      buttonClasses: ['btn', 'btn-sm'],
      applyClass: 'btn-danger',
      cancelClass: 'btn-inverse',
      startDate: moment().subtract(10, 'days').calendar()
    });

    //On Date Apply
    jQuery('.input-daterange-datepicker').on('apply.daterangepicker', function(ev:any, picker:any) {
      self.date.emit({
        start_date: picker.startDate.format('YYYY-MM-DD'),
        end_date: picker.endDate.format('YYYY-MM-DD')
      });
    });
  }
}



