import {Component, OnInit, AfterViewInit, ViewEncapsulation, Input, OnChanges, OnDestroy} from '@angular/core';
import { CalculatorAnalytics } from '../../services/calculator-analytics.service';
import {DateRangePickerComponent} from '../date_range_picker/date_range_picker.component';
import { Subscription } from 'rxjs/Subscription';

declare var jQuery: any;
declare var Morris: any;
declare var moment: any;

interface CollectiveData {
  uniqueVisitors: string;
  visitors: string;
  timeOnPage: string;
  conversions: number;
  conversionRate: string;
  avgResponse: string;
}

@Component({
  selector: 'og-overview',
  template: require('./overview.component.html'),
  encapsulation: ViewEncapsulation.None,
})

export class OverviewComponent implements OnInit, AfterViewInit, OnChanges, OnDestroy {
  @Input() calc: any;
  calc_id: string;
  start_date: String;
  end_date: String;
  postData: any = {
    start_date: new Date(moment().subtract(10, 'days').format('YYYY-MM-DD')).toISOString().substr(0,10),
    end_date: new Date(moment().add(1, 'day').format('YYYY-MM-DD')).toISOString().substr(0,10)
  };
  overviewChart: any;
  totalUniqueVisits: number = 0;
  totalVisits: number = 0;
  graphLoader: string = 'loading';
  private stats: CollectiveData;

  private subs: Subscription[] = [];

  constructor(private _calcAnalytics: CalculatorAnalytics) {
  }

  ngAfterViewInit() {

    this.overviewChart = Morris.Area({
      element: 'area-example',
      data: [{ y: '0', a: 0, b: 0 }],
      xkey: 'y',
      ykeys: ['a', 'b'],
      labels: ['Unique Views', 'Page Views'],
      fillOpacity: 0.4,
      hideHover: 'auto',
      behaveLikeLine: true,
      resize: true,
      pointFillColors: ['#fb545b'],
      pointStrokeColors: ['#269fd8'],
      lineColors: ['#fb545b', '#269fd8'],
    });
  }

  ngOnInit() {
    //code
  }

  ngOnChanges() {
    if(this.calc) {
      if (this.calc.liveApp)
        this.calc_id = this.calc._id;
      else
        this.calc_id = this.calc.parentApp;

      this.postData.calc_id = this.calc_id;
      //get stats
      this.subs.push(this.getViewStats());
      //get stats
      this.subs.push(this.getCombinedStats());
    }
  }

  getViewStats() {
    this.graphLoader = 'loading';
    this.postData.type = 'date';
    return this._calcAnalytics.getTrafficStats(this.postData)
      .subscribe(
        (response: any[]) => {
          if (response.length && response[0][0] != '(not set)') {
            this.totalUniqueVisits = 0;
            this.totalVisits = 0;
            response = response.map(val => {
              this.totalUniqueVisits += Number(val[1]);
              this.totalVisits += Number(val[2]);
              val[0] = val[0].replace(/(\d\d\d\d)(\d\d)(\d\d)/g, '$1-$2-$3');
              return { y: val[0], a: val[1], b: val[2] };
            });
            this.overviewChart.setData(response);

            response = response.map(
              val => { return { label: 'Visitors', value: val.b }; }
            );

            this.graphLoader = 'done';
          } else {
            this.graphLoader = 'No Data Available !';
          }
        },
        (error: any) => {
          this.graphLoader = 'Something Went Wrong !';
        }
      );
  }

  getCombinedStats() {
    // this.geoLoader = 'loading';
    this.postData.type = 'overview';
    return this._calcAnalytics.getTrafficStats(this.postData)
      .subscribe(
        (response: any[]) => {
          if (response.length) {
            this.stats = {
              uniqueVisitors: response[0][1],
              visitors: response[0][2],
              timeOnPage: (response[0][3] > 60) ? (response[0][3] / 60).toFixed(2) + ' min' : response[0][3].toFixed(2) + ' sec',
              conversions: response[0][4],
              conversionRate: (response[0][1] && response[0][4]) ? ((response[0][4] / response[0][1]) * 100).toFixed(2) : '0',
              avgResponse: response[0][5],
            };
            //this.geoLoader = 'done';
          } else {
            //this.geoLoader = 'No Data Available !';
            this.stats = {
              uniqueVisitors: '0',
              visitors: '0',
              timeOnPage: '0',
              conversions: 0,
              conversionRate: '0',
              avgResponse:'0'
            };
          }
        },
        (error: any) => {
          //this.geoLoader = 'Something Went Wrong !';
        }
      );
  }

  onDateSelect(date: any) {
    this.start_date = date.start_date;
    this.end_date = date.end_date;
    this.postData.start_date = new Date(date.start_date).toISOString().substr(0,10);
    this.postData.end_date = new Date(moment(new Date(date.end_date)).add(1, 'day').format('YYYY-MM-DD')).toISOString().substr(0,10);
    //refresh stats
    this.getViewStats();
    this.getCombinedStats();
  }

  ngOnDestroy() {
    this.unsubscribe();
  }

  unsubscribe() {
    this.subs.forEach(sub => sub.unsubscribe());
  }
}



